import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import MallHeader from '../../components/mallHeader'
import './style.less'
// 定义组件 
class mallDtl extends React.Component {
    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state = {
         dtlData:null
       }
    }
   render() { // return 里面写jsx语法 

      return ( 
        <div>
          <MallHeader title="详情" back={true}/>
          {
             this.state.dtlData
             ?(
               <div>
					<div className="imgBox">
			            <img src={this.state.dtlData.src}/>
			        </div>
			        <div className="contentBox">
	                    <h4>{this.state.dtlData.name}</h4>
	                    <p><span>id:{this.state.dtlData.id}</span><span>数量：{this.state.dtlData.num}</span></p>
	                    <p><span>类型：{this.state.dtlData.type}</span><span>传输：{this.state.dtlData.src.slice(0,4)}</span></p>
			        </div>
			        <div className="disc">
                        <p>{this.state.dtlData.cparagraph}</p>
			        </div>
			    </div>
             )
             :<div className="loadTip">正在加载中....</div>
          }

          
        </div>

      )
   }
   componentDidMount(){
     const id=this.props.params.id
     this.getList(id)
   } 
   getList(id){
	   fetch('https://www.easy-mock.com/mock/5a28a164b5c06d61d33714e9/llxf/react-demo-list').then((res)=>{
	       return res.json()
	   }).then((res)=>{
	       this.setState({
	          dtlData:res.data[id]
	       })
	   })
	}
}


export default mallDtl


