import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'

import MallHeader from '../../components/mallHeader'
import MallSlider from '../../components/mallSlider'
import List from './subPage/list'
import { connect } from 'react-redux'
import {showProducts} from '../../actions/product'

class mallHome extends React.Component {
    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state = {
         listData:[]
       }
    }
	render() {
	   return(
          <div>
            <MallHeader title="react-首页"  back={false} curCity={this.props.userinfo.cityName}/>
            <MallSlider />
            <List listData={this.props.products}/>
          </div>
	   )
        
	}
	componentDidMount(){
	   //this.getList()
	   this.props.showProducts_action()
	   //setTimeout(()=>{console.log(this.props.products)},2000)
	}

	//获取远程easy-mock数据，现在已不使用
	getList(){
	   fetch('https://www.easy-mock.com/mock/5a28a164b5c06d61d33714e9/llxf/react-demo-list').then((res)=>{
	       return res.json()
	   }).then((res)=>{
	       this.setState({
	          listData:res.data
	       })
	       console.log(this.state.listData)
	   })
	}
}

// -------------------redux react 绑定--------------------
const parseProducts = (products) => {
	let mapProduct = []
	for(let key in products) {
	  mapProduct.push(products[key])
	}
	return mapProduct
}

function mapStateToProps(state) {
    return {
        userinfo: state.userinfo,
        products: parseProducts(state.products)
    }
}

function mapDispatchToProps(dispatch) {
    return {
       showProducts_action(){
          dispatch(showProducts())
       }
    }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(mallHome)


