import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import {Link} from 'react-router'
import './style.less'

class List extends React.Component {
	render() {
	   return (
          <div>
             <h3>列表</h3>
             <ul className="list">
                {

                  this.props.listData.length
                  ?(
                     this.props.listData.map((item,index)=>{
                       return  <li key={index}>
                          <Link to={`/MallDtl/${index}`}>
  				                  <img src={item.src}/>
  				                  <div className="content">
  				                     <p className="itemTit">{item.name}</p>
  				                     <p className="itemInfo">{item.num}</p>
  				                  </div>
                          </Link>
				                 </li>

                     })
                     
                  )
                  :"列表加载中"
                }
             </ul>
          </div>
	   )
	}
	componentDidMount(){
	}
}

export default List


